import React, { useState } from "react";
import DiningLayout from "../layouts/DiningLayout";
import regions from "../data/regions.json";
import "../css/MyPage.css";

function MyPage() {
    const [isReviewModalOpen, setReviewModalOpen] = useState(false);
    const [isFavoriteModalOpen, setFavoriteModalOpen] = useState(false);
    const [isFollowModalOpen, setFollowModalOpen] = useState(false);

    // 지역 상태
    const [selectedProvince, setSelectedProvince] = useState(null);
    const [selectedCity, setSelectedCity] = useState(null);
    const [selectedArea, setSelectedArea] = useState(null);

    // 샘플 데이터
    const reviewList = [
        { id: 1, text: "리뷰 1", link: "/review/1" },
        { id: 2, text: "리뷰 2", link: "/review/2" },
        { id: 3, text: "리뷰 3", link: "/review/3" },
    ];
    const favoriteList = [
        { id: 1, text: "즐겨찾기 1", link: "/favorite/1" },
        { id: 2, text: "즐겨찾기 2", link: "/favorite/2" },
        { id: 3, text: "즐겨찾기 3", link: "/favorite/3" },
    ];
    const followList = [
        { id: 1, text: "팔로잉 1", link: "/follow/1" },
        { id: 2, text: "팔로잉 2", link: "/follow/2" },
        { id: 3, text: "팔로잉 3", link: "/follow/3" },
    ];

    const handleProvinceChange = (province) => {
        setSelectedProvince(province);
        setSelectedCity(null);
        setSelectedArea(null);
    };

    const handleCityChange = (city) => {
        setSelectedCity(city);
        setSelectedArea(null);
    };

    const handleAreaChange = (area) => {
        setSelectedArea(area);
    };

    return (
        <DiningLayout>
            <div className="mypage-container">
                <h1 className="mypage-title">마이페이지</h1>
                <div className="profile-section">
                    <div className="profile-left">
                        <div className="profile-picture">
                            <span>프로필 사진</span>
                        </div>
                        <div className="profile-tier"><img src="/images/silver.png" alt="실버" className="silver_image"/>실버
                        </div>
                    </div>
                    <div className="profile-right">
                        <p>팔로워: <span>XX명</span></p>
                        <p>팔로잉: <span>XX개</span></p>
                        <p>
                            지역 선택:
                            <select
                                value={selectedProvince || ""}
                                onChange={(e) => handleProvinceChange(e.target.value)}
                            >
                                <option value="">광역시도 선택</option>
                                {Object.keys(regions).map((province) => (
                                    <option key={province} value={province}>
                                        {province}
                                    </option>
                                ))}
                            </select>

                            {selectedProvince && (
                                <select
                                    value={selectedCity || ""}
                                    onChange={(e) => handleCityChange(e.target.value)}
                                >
                                    <option value="">시군구 선택</option>
                                    {Object.keys(regions[selectedProvince]).map((city) => (
                                        <option key={city} value={city}>
                                            {city}
                                        </option>
                                    ))}
                                </select>
                            )}
                        </p>
                        <p>리뷰 작성: <span>XX개</span></p>
                        <p>좋아요: <span>XX개</span></p>
                    </div>
                </div>
                <div className="action-buttons">
                    <button
                        className="action-btn"
                        onClick={() => setReviewModalOpen(true)}
                    >
                        리뷰 작성 내역
                    </button>
                    <button
                        className="action-btn"
                        onClick={() => setFavoriteModalOpen(true)}
                    >
                        즐겨찾기 내역
                    </button>
                    <button
                        className="action-btn"
                        onClick={() => setFollowModalOpen(true)}
                    >
                        팔로잉 내역
                    </button>
                </div>

                {/* 리뷰 작성 내역 모달 */}
                {isReviewModalOpen && (
                    <div className="modal-overlay" onClick={() => setReviewModalOpen(false)}>
                        <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                            <h2>리뷰 작성 내역</h2>
                            <ul>
                                {reviewList.map((review) => (
                                    <li key={review.id}>
                                        {review.text}
                                        <a href={review.link} className="go-to-review">
                                            바로가기
                                        </a>
                                    </li>
                                ))}
                            </ul>
                            <button onClick={() => setReviewModalOpen(false)}>닫기</button>
                        </div>
                    </div>
                )}

                {/* 즐겨찾기 내역 모달 */}
                {isFavoriteModalOpen && (
                    <div className="modal-overlay" onClick={() => setFavoriteModalOpen(false)}>
                        <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                            <h2>즐겨찾기 내역</h2>
                            <ul>
                                {favoriteList.map((favorite) => (
                                    <li key={favorite.id}>
                                        {favorite.text}
                                        <a href={favorite.link} className="go-to-favorite">
                                            바로가기
                                        </a>
                                    </li>
                                ))}
                            </ul>
                            <button onClick={() => setFavoriteModalOpen(false)}>닫기</button>
                        </div>
                    </div>
                )}

                {/* 팔로우 내역 모달 */}
                {isFollowModalOpen && (
                    <div className="modal-overlay" onClick={() => setFollowModalOpen(false)}>
                        <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                            <h2>팔로잉 내역</h2>
                            <ul>
                                {followList.map((follow) => (
                                    <li key={follow.id}>
                                        {follow.text}
                                        <a href={follow.link} className="go-to-follow">
                                            바로가기
                                        </a>
                                    </li>
                                ))}
                            </ul>
                            <button onClick={() => setFollowModalOpen(false)}>닫기</button>
                        </div>
                    </div>
                )}
            </div>
        </DiningLayout>
    );
}

export default MyPage;